body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }
.wrapper {
    margin: 0 60px 60px; }
.container {
    max-width: 1250px!important; }
.divider {
    padding-bottom: 1rem;
    .divider_cursor {
        width: 20%;
        height: 2px;
        background-color: #052230; } }
.navbar {
    margin-bottom: 40px;
    .navbar-item {
        font-weight: 600;
        line-height: 3rem;
        padding: 1.5rem 2rem;
        &:hover {
            color: #00d1b2; } }
    .navbar-brand {
        background: #fff;
        margin-top: -40px;
        margin-bottom: -40px;
        .navbar-item {
            line-height: auto;
            img {
                max-height: 100px; } } }
    .navbar-menu {
        padding: 0px 18px; } }
.header {

    .hero {
        background-image: url('http://capethemes.com/demo/religionis/wp-content/uploads/2019/10/people-2597796_1920.jpg');
        background-position: center;
        background-size: cover;
        background-attachment: fixed;

        .hero-body {
            background-color: rgba(5,34,48,0.5);

            transition: background .3s,border-radius .3s,opacity .3s;

            .hero-content {
                width: 60%;

                .title {
                    font-size: 59px;
                    line-height: 1.1em; }

                .divider {
                    padding-bottom: 1rem;
                    .divider_cursor {
                        width: 20%;
                        height: 2px;
                        background-color: #fff; } }
                .subtitle {
                    color: #fff;
                    margin-top: 1rem; } } } } }
.home_missions {
    margin-top: 5em;
    .mission {
        .picture {
            margin-bottom: 1rem;
            img {
                width: 58px; } }
        .title {
            font-size: 24px; }
        .subtitle {
            font-size: 15px; }
        .content {
            font-size: 14px; }

        &.is-big {
            padding: 8rem 3rem;
            margin-top: -10rem;
            margin-right: -16.7rem;
            background: #e7ae43;
            height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            .title,.subtitle {
                color: #fff; }
            .title {
                font-size: 29px;
                line-height: 1.2em; }
            .subtitle {
                line-height: 1.5em; } } } }
.latest_news {
    margin-bottom: 6rem;
    .intro {
        text-transform: uppercase;
        font-weight: 900;
        margin-bottom: 1rem;
        padding-bottom: 1em; } }

.home_footer {

    .hero {
        background-image: url('http://capethemes.com/demo/religionis/wp-content/uploads/2019/10/people-2604831_1920.jpg');
        background-position: center;
        background-size: cover;
        background-attachment: fixed;
        height: 90vh;
        .hero-body {
            justify-content: flex-end;
            align-items: center;
            padding: 0px;
            height: 90vh;
            display: flex;
            .hero-content {
                width: 60%;
                background: #e7ae43;
                padding: 5rem;
                .title {
                    font-size: 29px;
                    line-height: 1.1em;
                    max-width: 400px; }
                .divider {
                    padding-bottom: 1rem;
                    .divider_cursor {
                        width: 20%;
                        height: 2px;
                        background-color: #fff; } }
                .subtitle {
                    color: #fff;
                    margin-top: 1rem;
                    max-width: 400px; } } } } }

.footer {
    background: #e7ae43; }
